  .collapse-accordion ul {
      list-style: none;
      margin: 0;
      padding: 0;
  }

  .collapse-accordion label {
      display: block;
      cursor: pointer;
      padding: 10px 25px;
      border: 1px solid #fff;
      border-bottom: none;
      background-color: var(--text-tertiary);
      position: relative;
  }

  .collapse-accordion label:hover {
      background: #999;
  }

  .collapse-accordion label:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      text-indent: -9999px;
      border-top: 1px solid var(--accent-black);
      border-left: 1px solid var(--accent-black);
      -webkit-transition: all .3s ease-in-out;
      transition: all .3s ease-in-out;
      text-decoration: none;
      color: transparent;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      transform: rotate(135deg);
      right: 40px;
      top: 50%;
      margin-top: -5px;
  }

  .collapse-accordion input[type="checkbox"]:checked+label:after {
      transform: rotate(45deg);
      top: 20px;
  }

  .collapse-accordion input[type="radio"]:checked+label:after {
      transform: rotate(45deg);
      top: 20px;
  }

  .collapse-accordion label.last {
      border-bottom: 1px solid #fff;
  }

  .collapse-accordion ul ul li {
      padding: 10px;
      background: rgba( 15,4,46,.20);
  }


  .collapse-accordion input[type="checkBox"] {
      position: absolute;
      left: -9999px;
  }
  .collapse-accordion input[type="radio"] {
      position: absolute;
      left: -9999px;
  }

  .collapse-accordion input[type="checkBox"]~ul {
      height: 0;
      transform: scaleY(0);
    transition: transform .2s ease-out;
  }
  .collapse-accordion input[type="radio"]~ul {
      height: 0;
      transform: scaleY(0);
transition: transform .5s ease-out;
  }

  .collapse-accordion input[type="checkBox"]:checked~ul {
      height: 100%;
      transform-origin: top;
      transition: transform .5s ease-out;
      transform: scaleY(1);
  }

  .collapse-accordion input[type="radio"]:checked~ul {
      height: 100%;
      transform-origin: top;
      transition: transform .2s ease-out;
      transform: scaleY(1);
  }

  .collapse-accordion input[type="checkBox"]:checked+label {
      background: var(--text-tertiary);
      border-bottom: 0px solid #fff;
  }
  .collapse-accordion input[type="radio"]:checked+label {
      background: var(--text-tertiary);
      border-bottom: 1px solid #fff;
  }

  .collapse-accordion input[type="checkbox"]:checked+label .collpaseText {
      display: block;
  }

  .collapse-accordion input[type="radio"]:checked+label .collpaseText {
      display: block;
  }

  .collapse-accordion input[type="checkbox"]:checked+label .expandText {
      display: none;
  }

  .collapse-accordion input[type="radio"]:checked+label .expandText {
      display: none;
  }

  .collpaseText {
      display: none;
  }




